import JsonEditor from '@/components/jsonEditor';
import { createUpDictionary, createDownDictionary } from '@/api/equipmentData/dictionary';
export default {
  components: {
    JsonEditor: JsonEditor
  },
  data: function data() {
    return {
      visible: false,
      value: '',
      mode: 'up'
    };
  },
  methods: {
    init: function init(mode) {
      this.mode = mode;
      this.visible = true;
    },
    submit: function submit() {
      var _this = this;

      switch (this.mode) {
        case 'up':
          createUpDictionary(JSON.parse(this.value)).then(function () {
            _this.success();
          });
          break;

        case 'down':
          createDownDictionary(JSON.parse(this.value)).then(function () {
            _this.success();
          });
          break;
      }
    },
    success: function success() {
      var _this2 = this;

      this.$message({
        type: 'success',
        message: '新增成功',
        duration: 500,
        onClose: function onClose() {
          _this2.visible = false;

          _this2.$emit('submit');
        }
      });
    },
    onClose: function onClose() {
      this.value = '';
      this.$parent.insertView = false;
    }
  }
};